import Vue from 'vue'
import Router from 'vue-router'
import { components, AmplifyEventBus } from 'aws-amplify-vue'
import store from '@/store'
// import About from '@/views/About.vue'
const About = () => import('@/views/About.vue')
// import CheckIn from '@/views/CheckIn.vue'
const CheckIn = () => import('@/views/CheckIn.vue')
// import PreCheckIn from '@/views/PreCheckIn.vue'
const PreCheckIn = () => import('@/views/PreCheckIn.vue')
// import Guide from '@/views/Guide.vue'
const Guide = () => import('@/views/Guide.vue')
// import Key from '@/views/Key.vue'
const Key = () => import('@/views/Key.vue')

import { CallDialog } from '@/utils'

Vue.use(Router)

AmplifyEventBus.$on('authState', async (state) => {
  // console.log('AmplifyEventBus.$on', state)

  if (state === 'signedOut'){
    console.log('signedOut')

    router.push({path: '/auth'})
  } else if (state === 'signedIn') {
    console.log('signedIn')

    const error = await store.dispatch('session/login').catch(e => {
      console.error('login!!!', e)
      return e
    })

    if (error) {
      window.alert(error)
      await store.dispatch('session/logout').catch(e => console.error('login!!!', e))
      return
    }
    
    router.push({path: '/'})
  }
});

const router = new Router({
  // mode: 'hash',
  mode: 'history',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'About',
      component: About,
      meta: { 
        requiresAuth: true, 
        requiresIdVerified: false,
        requiresIdsMatch: false
      }
    },
    {
      path: '/checkIn',
      name: 'CheckIn',
      component: CheckIn,
      meta: { 
        requiresAuth: true, 
        requiresIdVerified: true,
        requiresIdsMatch: false
      }
    },
    {
      path: '/preCheckIn',
      name: 'PreCheckIn',
      component: PreCheckIn,
      meta: { 
        requiresAuth: true, 
        requiresIdVerified: false,
        requiresIdsMatch: false
      }
    },
    {
      path: '/guide',
      name: 'Guide',
      component: Guide,
      meta: { 
        requiresAuth: true, 
        requiresIdVerified: true,
        requiresIdsMatch: false
      }

    },
    {
      path: '/key',
      name: 'Key',
      component: Key,
      meta: { 
        requiresAuth: true, 
        requiresIdVerified: true, 
        requiresIdsMatch: true
      }

    },
    {
      path: '/auth',
      name: 'Authenticator',
      component: components.Authenticator
    },
    {
      path: '/confirmSignUp',
      name: 'ConfirmSignUp',
      component: components.ConfirmSignUp,
      props: {
        confirmSignUpConfig: {
          header: 'Confirm Sign Up'
        }
      }
    },
  ]
})

router.beforeEach(async (to, from, next) => {

  console.log("beforeEach store.state.session", JSON.stringify(store.state.session))

  if (to.matched.some(record => record.meta.requiresAuth)) {
    const session = store.state.session
    const checkIn = store.state.checkIn
    
    if (!session.username || !session.identityId ) {
      CallDialog({message: "Session timeout. Please re-login."})

      return await store.dispatch('session/logout')
        .catch(e => console.log('login!!!', e))
    }
  }

  return next()
})

router.beforeResolve(async (to, from, next) => {

  if (to.matched.some(record => record.meta.requiresIdVerified)) {
    // console.log('store.state.checkIn.members', store.state.checkIn.members)
    // console.log('store.state.checkIn.reservation.memberCount', store.state.checkIn.reservation.memberCount)

    // const allMembersInput = store.state.checkIn.members.length === store.state.checkIn.reservation.memberCount
    const allIdVerified = !store.state.checkIn.members.some(crtMember => !crtMember.idVerified)

    // console.log('allMembersInput', allMembersInput)
    console.log('allIdVerified', allIdVerified)

    if (!allIdVerified) {
      CallDialog({message: "Need the host to verify all the ID photos first."})  
      return next(false)
    } 
  }

  if (to.matched.some(record => record.meta.requiresIdsMatch)) {

    // const allMembersInput = store.state.checkIn.members.length === store.state.checkIn.reservation.memberCount
    const allIdMatches = !store.state.checkIn.members.some(crtMember => !crtMember.idMatches)

    // console.log('allMembersInput', allMembersInput)
    console.log('allIdMatches', allIdMatches)

    if (!allIdMatches) {
      CallDialog({message: "Need all the ID pictures to match the members' pictures."})
      return next(false)
    } 
  }

  return next()
})

export default router